import React, { useState } from 'react'
import DocLayout from '../components/layout/doc';
import Layout from "../components/layout"
import CodeExample from '../components/CodeExample';
import CodeFormatContext from '../components/contexts/CodeFormatContext';

const TextSection = () => (
    <section>
        TextSection
    </section>
)

const modalExample = (`\
<div class='modal'>
    <div>
        <header>Mon header</header>
        <div>Mon body</div>
        <footer>Mon footer</footer>
    </div>
</div>
`)

const heroExample = (`\
<section class="hero background-primary">
    <footer class="container">
        Foot
    </footer>
    <div class="measure">
        <p>
        Mon paragraphe
        </p>
    </div>
    <header>
        Head
    </header>
</section>
`)

const DisplaySection = () => {

    const [showModal, setShowModal] = useState(false)

    const toggleShowModal = () => {
        setShowModal(!showModal)
    }

    return (
        <section>

            <h2>Heros</h2>

            <div className={`modal ${showModal && 'active'}`}>
                <div>
                    <header>Mon header</header>
                    <div className='background-white'>Mon body</div>
                    <footer className='text-right'><button onClick={toggleShowModal} className='danger'>Fermer</button></footer>
                </div>
            </div>


            <div className='columns'>
                <div className='column'>

                    <CodeExample code={heroExample} />
                </div>
                <div className='column padding-top-1'>

                    <div dangerouslySetInnerHTML={{ __html: heroExample }} />

                </div>
            </div>

            <h2>Modals</h2>

            <div class={`modal ${showModal && 'active'}`}>
                <div>
                    <header>Mon header</header>
                    <div className='background-white'>Mon body</div>
                    <footer className='text-right'><button onClick={toggleShowModal} className='danger'>Fermer</button></footer>
                </div>
            </div>


            <div className='columns'>
                <div className='column'>

                    <CodeExample code={modalExample} />
                </div>
                <div className='column padding-top-1' style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>

                    <button className='primary' onClick={toggleShowModal}>Click to test</button>
                </div>
            </div>

        </section>
    )
}

const HelpersSection = () => (
    <section>
        HelpersSection
    </section>
)

const FormsSection = () => (
    <section>
        FormsSection
    </section>
)

const MenuItem = ({ children, onClick }) => (

    <div onClick={onClick} className='box text-center margin-1 hover-primary' style={{ width: '65px' }}>
        {children}
    </div>
)

export default () => {

    const [modalOpen, setModalOpen] = useState(false)
    const [section, setSection] = useState('text')

    const [format, setFormat] = useState(/*localStorage.getItem('code-format') || */'html')

    const changeFormat = format => {
        // localStorage.setItem('code-format', format)
        setFormat(format)
    }

    //style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}
    return (
        <Layout>

            {/* <div className='bottom-1 right-1 height-2 width-2 background-primary text-center cursor-pointer' style={{
                position: 'fixed', borderRadius: '50%', display: 'flex'
                , justifyContent: 'center', alignItems: 'center',
                zIndex: 99
            }}
                onClick={toggleModalOpen}
            >
                {
                    modalOpen ? <i className='fas fa-times' /> : <i className='fas fa-exclamation' />
                }
            </div> */}

            <div className={`modal ${modalOpen && 'active'}`}>
                <div className='background-white' style={{ height: '90%', width: '90%' }}>
                    Cheat Sheet
                </div>
            </div>

            <div className='container margin-top-1 margin-bottom-1'>

                <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}>

                    <MenuItem onClick={() => { setSection('text') }}>
                        <div className='height-2'>
                            <i className='fas fa-paragraph fa-2x' />
                        </div>
                        <small>Text</small>
                    </MenuItem>

                    <MenuItem onClick={() => { setSection('display') }}>
                        <div className='height-2'>
                            <i className='fas fa-th-large fa-2x' />
                        </div>
                        <small>Display</small>
                    </MenuItem>

                    <MenuItem onClick={() => { setSection('helpers') }}>
                        <div className='height-2'>
                            <i className='fas fa-tools fa-2x' />
                        </div>
                        <small>Helpers</small>
                    </MenuItem>

                    <MenuItem onClick={() => { setSection('forms') }}>
                        <div className='height-2'>
                            <i className='fas fa-bars fa-2x' />
                        </div>
                        <small>Forms</small>
                    </MenuItem>

                </div>

                <hr />

                <CodeFormatContext.Provider value={{
                    format, setFormat: changeFormat
                }}>

                    {
                        section === 'text' ? <TextSection /> :
                            section === 'display' ?
                                <DisplaySection />
                                : section === 'helpers' ?
                                    <HelpersSection /> :
                                    <FormsSection />
                    }

                </CodeFormatContext.Provider>

            </div>

        </Layout>
    )
}