import React, { useState } from 'react'
import CodeFormatContext from './contexts/CodeFormatContext';

const selectText = (e) => {

    var range = document.createRange();
    range.selectNode(e.target);
    window.getSelection().removeAllRanges();
    window.getSelection().addRange(range);
    document.execCommand('copy');
    window.getSelection().removeAllRanges();
};

const Highlight = ({ code }) => {

    return (
        <pre>
            {code}
        </pre>
    )
}

const reactFormat = code => (
    code.replace(/(class=)/g, 'className=')
)

const CodeExample = ({ code }) => {

    const [copied, setCopied] = useState(false)

    const onClick = e => {

        setCopied(true)
        setTimeout(() => { setCopied(false) }, 1000)

        selectText(e)
    }


    return (
        <CodeFormatContext.Consumer>
            {({ format, setFormat }) => (
                <>
                    <button className={`small ${format === 'html' ? 'primary' : 'color-grey'}`} onClick={() => { setFormat('html') }}>HTML</button>
                    {' '}
                    <button className={`small ${format === 'react' ? 'primary' : 'color-grey'}`} onClick={() => { setFormat('react') }}>ReactJS</button>

                    <div className='codeexample background-white' onClick={onClick} onChange={() => { }} style={{ position: 'relative' }}>

                        <Highlight code={format === 'html' ? code : reactFormat(code)} />
                        <div style={{ position: 'absolute', top: 10, right: 20, fontSize: 10, opacity: 0.5 }}>
                            {copied ? 'Copied!' : 'Click to copy on clipboard'}
                        </div>
                    </div>
                </>
            )}
        </CodeFormatContext.Consumer>
    )
}

export default CodeExample;